@import url('https://fonts.googleapis.com/css?family=Roboto+Mono');
@import url('https://fonts.googleapis.com/css2?family=Fredoka:wght@300;400;500;600;700&family=Lato:ital,wght@0,100;0,300;0,400;0,700;0,900;1,100;1,300;1,400;1,700;1,900&family=Montserrat:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&family=Roboto:ital,wght@0,300;0,400;0,500;0,700;0,900;1,700&display=swap');

/* BODY */
.App {
  text-align: center;
  font-family:"Montserrat", sans-serif;
  color: black;

}

.main{
  display: flex;
  flex-direction: column;
}

/*NAV BAR=============================================*/

nav{
  width:100%;
  height: fit-content;
  padding:0.5rem 0rem;
  top:0px;
  background-color: white;
  z-index: 1;
  margin-bottom:10px;
  position:fixed;
}
nav ul{
  margin: auto;
  width: 360px;
  padding: 0px;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;

}

.navElement .anchor{
  text-decoration: none;
  color: black;
  padding: 5px;
  font-size: 0.8rem;
  transition: 0.25s;
  margin: 5px;
}

.navElement .anchor:hover{
  opacity: 1;
  color: white;
  background-color: black;
  transition: 0.2s;

}
#myname{
  padding: 3px 15px;
  font-size: 0.7rem;
  font-family: "lato";
}

.content{
  margin-top:1.5rem;
}
/* HERO ==================================*/
.hero{
  font-family: "lato";
}
.hero p{
  font-size: 1.4vh;

}
.slogan{
  padding: 1.5rem 0rem;
}
.hero hr{
  margin:auto;
  width: 45vw;
}

.hero-intro{
  margin: auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width:70%;
}
/*Book===========================*/

.book--content{
  margin: auto;
  width: 80%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-top: 4vh;
}
.book-img{
  width:30%;
  border: 5px ridge white;
  height: auto;
}
.book--content a{
  text-decoration: none;
  font-size: 0.65rem;
  cursor: pointer;
  margin: 1rem 0rem 1rem 0rem;
  background-color: black;
  padding: 10px;
  color: white;
  font-weight: bold;
}

.book--content a:hover{
  background-color: rgb(40,40,40);
}
/*PUBLICATION */


.publication{
  margin: auto;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-self: center;
  background-color: white;
}
.translation{
  padding: 3vh 8vw 3vh 8vw;
  text-align: justify;
  width: 100%;

}
#racism{
  margin: 0vh 0vw 4vh 0vw;
  font-size: 2rem;
  text-align: center;
}

.publication p{
  font-size: 0.8rem;
}
.publication img{

  width: 100%;
  margin: auto;
}



/*Footer ========*/

.footer{
  position: absolute;
  bottom: -50;
  width:100vw;
  padding: 2rem 0rem 2rem 0rem;

}

.footer hr, nav hr{
  width: 35%;
  margin:auto;
  margin-top: 1rem;
  margin-bottom: 1rem;
}
.footer span{
  font-size: 14px;
  color: black;
  padding: 10px;
}

.footer span:hover{
  color: #1781C2;
  opacity: 1;
}
.footer p{
  margin: 1rem 0rem 0.5rem 0rem;
  font-size: 8px;
}


/*Project */
.project-container, .home-container{
  width: 80%;
  margin:auto;
  margin-top: 3rem;
  padding: 3rem 0rem 3rem 0rem;
  border: 1px solid gray;
  ;
}
.project-container{
  display: flex;
  flex-direction: column;
}
.lili{
  color:black; 
  text-decoration: none;
  font-size: 14px;
  margin-bottom: 10px;
}
.lili:hover{
  color:#1781C2;
}
/*===========================PAGE NOT FOUND */ 

p{
  font-size: 12px;
}

/* STUDIO 243 */
.studio243{
  max-width: 50%;

  
}

.back-to-top {
  position: fixed;
  bottom: 20px;
  right: 20px;
  font-size: 20px;
  background: rgb(40,40,40);
  color: white;
  cursor: pointer;
  border-radius: 100px;
  border: none;
  height: 40px;
  width: 40px;
  /*box-shadow: 0 5px 10px #ccc;*/
}

.back-to-top:hover {
  background: #1781C2;
}


@media (max-width: 450px){
  .studio243{
    max-width: 100%;
  }
}